.gallery {
  padding: 100px 0 60px 0;
  &__title {
    margin-bottom: 100px;
  }
}

.gallery-items {
  display: grid;
  grid-template-columns: 1fr;
  grid-column-gap: 20px;
  max-width: 1159px;
  margin: 0 auto 50px;
  &__left {
    display: none;
    &__my {
      width: 378px;
      height: 268px;
    }
    &__white {
      width: 378px;
      height: 263px;
    }
    &__eve {
      width: 378px;
      height: 290px;
    }
    &_m-bot {
      &:not(:last-child) {
        margin-bottom: 20px;
      }
    }
  }
  &__mid {
    display: none;
    &__city {
      width: 378px;
      height: 380px;
    }
    &__night {
      width: 378px;
      height: 278px;
    }
    &__my {
      width: 378px;
      height: 268px;
    }
    &_m-bot {
      &:not(:last-child) {
        margin-bottom: 20px;
      }
    }
  }
  &__right {
    margin: 0 auto;
    &__many {
      width: 280px;
      height: 405px;
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      &_top-size {
        width: 49.5%;
        height: 118px;
      }
      &_bot-size {
        width: 33%;
        height: 92px;
      }
    }
    &__old {
      width: 280px;
      height: 303px;
    }
    &_m-bot {
      &:not(:last-child) {
        margin-bottom: 20px;
      }
    }
  }
  @media screen and (min-width: 768px) {
    grid-template-columns: 1fr 1fr;
    &__mid {
      display: unset;
    }
    &__right {
      &__many {
        width: 333px;
        height: 452px;
        &_top-size {
          height: 168px;
        }
      }
      &__old {
        width: 333px;
        height: 353px;
      }
    }
  }
  @media screen and (min-width: 1440px) {
    grid-template-columns: 1fr 1fr 1fr;
    &__left {
      display: unset;
    }
    &__right {
      &__many {
        width: 363px;
        height: 462px;
      }
      &__old {
        width: 363px;
        height: 373px;
      }
    }
  }
}

.gallery-items_hover {
  position: absolute;
  width: 100%;
  height: 100%;
  color: $white;
  display: flex;
  align-items: center;
  transform: translateX(-100%);
  transition: all 0.2s ease-out;
  &__block {
    width: 45px;
    height: 45px;
    background-color: #00cbf6;
    display: flex;
    justify-content: center;
    align-items: center;
    left: 5%;
    &:not(:last-child) {
      margin-right: 20px;
    }
  }
  &::before {
    content: "";
    display: block;
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: #000;
    opacity: 0.64;
  }
}

.gallery-item {
  overflow: hidden;
  position: relative;
}

.gallery-item:hover > .gallery-items_hover {
  transform: translateX(0);
  transition: all 0.2s ease-out;
}

.gallery-items_hover__block-sm {
  width: 25px;
  height: 25px;
}
