* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 16px;
}

a {
  text-decoration: none;
  color: $white;
  position: relative;
}
img {
  width: 100%;
  height: 100%;
}

.container {
  padding: 0 15px;
  max-width: 1920px;
  margin: 0 auto;
  @media screen and (min-width: 1024px) {
    padding: 0 100px;
  }
  @media screen and (min-width: 1920px) {
    padding: 0 380px;
  }
}

.btn {
  min-width: 170px;
  padding: 16px;
  outline: none;
  border: 0;
  background-color: $green;
  color: $white;
  text-transform: uppercase;
  border-radius: 5px;
  font-weight: 700;
  position: relative;
  box-shadow: 0 -5px 0 $white;
  cursor: pointer;
  transition: all 0.2s;
  &:active {
    color: $green;
    background-color: $white;
    box-shadow: 0 -5px 0 $green;
    transition: all 0.2s;
  }
}

.load-btn {
  display: flex;
  margin: 0 auto;
  box-shadow: none;
  align-items: center;
  &__plus {
    margin-right: 15px;
  }
}

.title {
  color: $title;
  font-size: 2.25em; //35px
  line-height: 1.111111111111111em; //40px
  text-align: center;
  position: relative;
  font-weight: 400;
  &::after {
    content: "";
    display: block;
    width: 80px;
    height: 2px;
    background-color: $green;
    position: absolute;
    left: 0;
    right: 0;
    margin: 0 auto;
    bottom: -40%;
  }
  &::before {
    content: "";
    display: block;
    width: 80px;
    height: 2px;
    background-color: $green;
    position: absolute;
    left: 0;
    right: 0;
    margin: 0 auto;
    bottom: -55%;
    transform: translateX(20%);
  }
}
