.services {
  padding: 80px 0 100px 0;
  &__title {
    margin-bottom: 70px;
  }
}

.services-btns {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr;
  &__btn {
    width: 100%;
    min-height: 30px;
    justify-self: center;
    background-color: #f8fcfe;
    border: 0;
    outline: none;
    position: relative;
    border: 1px solid #edefef;
    transition: all 0.2s;
    margin-bottom: 20px;
    color: #717171;
    font-size: 0.8em;
    &:not(:last-child) {
      border-right: 1px solid transparent;
    }
    &:hover {
      border: 1px solid $green;
    }
    &:focus {
      background-color: $green;
      border: 1px solid $green;
      color: $white;
      &::after {
        content: "";
        display: block;
        border: 7px solid transparent;
        border-top: 7px solid $green;
        position: absolute;
        bottom: -50%;
        left: 50%;
        transform: translateX(-50%);
      }
    }
  }
  @media screen and (min-width: 768px) {
    display: none;
    &__btn {
      font-size: 1em;
      min-height: 76px;
      &:focus {
        &::after {
          bottom: -20%;
        }
      }
    }
  }
}

.services-btns-tablet {
  display: none;
  @media screen and (min-width: 768px) {
    display: flex;
  }
}

.services-block {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 auto;
  &__img-container {
    width: 190px;
    height: 140px;
    overflow: hidden;
    margin-bottom: 20px;
  }
  &__text {
    color: $text;
    font-size: 0.875em; //14px
    line-height: 1.714285714285714em; //24px
    text-align: center;
  }
  @media screen and (min-width: 768px) {
    flex-direction: unset;
    &__img-container {
      margin-bottom: unset;
      flex-shrink: 0;
      margin-right: 10px;
    }
    &__text {
      text-align: left;
      max-width: 950px;
    }
  }
}
