.header {
  background: url(~img/bg-header.png) no-repeat center;
  background-size: cover;
  height: 100vh;
  overflow: hidden;
  width: 100%;
  &__container-nav {
    padding-top: 30px;
    padding-bottom: 30px;
    background-color: rgba(94, 93, 88, 0.6);
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
  }
  &__search {
    margin-left: auto;
    margin-right: 5%;
    font-size: 1.5em;
    color: $white;
    opacity: 0.6;
    cursor: pointer;
    &:hover {
      color: $green;
      opacity: unset;
    }
  }
  @media screen and (min-width: 768px) {
    background-position: top;
    height: 1012px;
    &__search {
      margin: unset;
      order: 3;
    }
  }
  &__container-home {
    padding-top: 100px;
    @media screen and (min-width: 768px) {
      padding-top: 270px;
    }
  }
}

.logo {
  display: flex;
  align-items: center;
  order: 0;
  &__hexagon {
    font-weight: 700;
    font-size: 2.5em; //40px
    line-height: 1.290322580645161em; //31em
    color: $white;
    background-color: $green;
    width: 50px;
    height: 33px;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 5px;
    &::before {
      content: "";
      position: absolute;
      display: block;
      border: 25px solid transparent;
      border-bottom: 15px solid $green;
      top: -40px;
    }
    &::after {
      content: "";
      position: absolute;
      display: block;
      border: 25px solid transparent;
      border-top: 15px solid $green;
      bottom: -40px;
    }
  }
  &__text {
    color: $white;
    font-size: 2em; //32px
    line-height: 1.5em; //48px
    display: none;
    @media screen and (min-width: 1024px) {
      display: unset;
    }
  }
  &__text-green {
    color: $green;
    font-weight: 700;
  }
}

.toggle-burger {
  display: none;
}

.burger {
  width: 25px;
  height: 20px;
  position: relative;
  overflow: hidden;
  &__line {
    height: 3px;
    position: absolute;
    right: 0;
    background-color: $green;
    border-radius: 10px;
    transition: all 0.2s linear;
  }
  &__line-top {
    width: 25px;
    top: 0;
  }
  &__line-mid {
    width: 15px;
    top: 50%;
    transform: translateY(-50%);
  }
  &__line-bot {
    width: 25px;
    bottom: 0;
  }
  @media screen and (min-width: 768px) {
    display: none;
  }
}

.toggle-burger:checked + label > .burger > .burger__line-top {
  transform: translate(0, 8px) rotate(-45deg);
  transition: all 0.1s linear 0.1s;
}
.toggle-burger:checked + label > .burger > .burger__line-mid {
  transform: translateX(-200%);
  transition: all 0.1s linear;
}
.toggle-burger:checked + label > .burger > .burger__line-bot {
  transform: translate(0, -9px) rotate(45deg);
  transition: all 0.1s linear 0.2s;
}

.toggle-burger:checked ~ nav > .nav__link {
  color: #fff;
  transform: unset;
}
.toggle-burger:checked ~ nav > .nav__link:nth-child(1) {
  transition: all 0.1s 0.7s ease-out;
}
.toggle-burger:checked ~ nav > .nav__link:nth-child(2) {
  transition: all 0.1s 0.6s ease-out;
}
.toggle-burger:checked ~ nav > .nav__link:nth-child(3) {
  transition: all 0.1s 0.5s ease-out;
}
.toggle-burger:checked ~ nav > .nav__link:nth-child(4) {
  transition: all 0.1s 0.4s ease-out;
}
.toggle-burger:checked ~ nav > .nav__link:nth-child(5) {
  transition: all 0.1s 0.3s ease-out;
}
.toggle-burger:checked ~ nav > .nav__link:nth-child(6) {
  transition: all 0.1s 0.2s ease-out;
}
.toggle-burger:checked ~ nav > .nav__link:nth-child(7) {
  transition: all 0.2s ease-out;
}

.nav {
  position: absolute;
  transform: translateY(214px);
  display: flex;
  flex-direction: column;
  left: 0;
  right: 0;
  overflow: hidden;
  &__link {
    padding: 15px 0;
    width: 100%;
    text-transform: uppercase;
    text-align: center;
    color: transparent;
    transform: translateY(-1000px);
    transition: all 0.5s ease-out;
    position: relative;
    background-color: rgba(94, 93, 88, 0.8);
    font-weight: 700;
    &:hover::before {
      content: "";
      display: block;
      height: 30px;
      width: 6px;
      background-color: $green;
      position: absolute;
      left: 2%;
      top: 50%;
      transform: translateY(-50%);
    }
  }
  @media screen and (min-width: 768px) {
    position: unset;
    flex-direction: unset;
    transform: unset;
    order: 1;
    margin-left: auto;
    margin-right: 20px;
    &__link {
      color: $white;
      opacity: 0.6;
      position: unset;
      transform: unset;
      background-color: unset;
      font-weight: unset;
      &:not(:last-child) {
        margin-right: 5%;
      }
      &:hover {
        color: $green;
        opacity: unset;
        &::before {
          display: none;
        }
      }
    }
  }
}

.h-group {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  color: $white;
  margin-bottom: 100px;
  &__pre-title {
    text-transform: uppercase;
    font-size: 1.5em; //24px
    margin-bottom: 20px;
    &-green {
      color: $green;
    }
  }
  &__title {
    font-size: 3em; //48px
    text-transform: uppercase;
    margin: 0;
    margin-bottom: 20px;
  }
  &__subtitle {
    color: #d4d4d4;
    font-weight: 400;
  }
  @media screen and (min-width: 1024px) {
    margin-bottom: 135px;
    &__pre-title {
      font-size: 2.1875em; //35px
    }
    &__title {
      font-size: 4.875em; //78px
    }
    &__subtitle {
      font-size: 2.125em; //34px
    }
  }
}

.header-btns {
  display: flex;
  flex-direction: column;
  align-items: center;
  &__exp {
    margin-bottom: 20px;
  }
  &__purchase {
    background: #00cbf6;
  }
  @media screen and (min-width: 1024px) {
    flex-direction: unset;
    justify-content: center;
    &__exp {
      margin-bottom: unset;
      margin-right: 20px;
    }
  }
}
