.feedback {
  padding: 100px 0;
  background: url(~img/bg-feedback.png) no-repeat;
  background-size: cover;
  &__title {
    margin-bottom: 100px;
    color: $white;
  }
}

.feedback-client {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  margin-bottom: 50px;
  &__quotes {
    font-size: 4em;
    color: $green;
    margin-bottom: 35px;
  }
  &__text {
    font-weight: 300;
    max-width: 1160px;
    line-height: 1.875em; //30px
    margin-bottom: 35px;
    color: $feedback-text;
  }
  &__name {
    text-transform: uppercase;
    color: $green;
    font-weight: 700;
    margin-bottom: 15px;
  }
  &__post {
    color: $text;
    margin-bottom: 50px;
    color: $feedback-text;
  }
  &__img-container {
    width: 142px;
    height: 142px;
    position: relative;
    z-index: 1;
    &::before {
      content: "";
      display: block;
      width: 110%;
      height: 110%;
      border-radius: 50%;
      background-color: $green;
      opacity: 0.3;
      position: absolute;
      left: -5%;
      top: 50%;
      transform: translateY(-50%);
      z-index: -1;
    }
  }
}

.carousel {
  display: flex;
  align-items: center;
  justify-content: space-between;
  max-width: 485px;
  margin: 0 auto;
  &__btn {
    width: 30px;
    height: 30px;
    border: 1px solid $feedback-text;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1.3em;
    color: $feedback-text;
    cursor: pointer;
    &:hover {
      background-color: $green;
      color: $white;
      border: 1px solid transparent;
    }
  }
  &__img-container {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    z-index: 1;
    position: relative;
    transition: all 0.2s;
    cursor: pointer;
    &::before {
      content: "";
      display: block;
      width: 110%;
      height: 110%;
      border-radius: 50%;
      background-color: $green;
      opacity: 0.3;
      position: absolute;
      left: -5%;
      top: 50%;
      transform: translateY(-50%);
      z-index: -1;
    }
    @media screen and (min-width: 375px) {
      width: 60px;
      height: 60px;
    }
  }
}
.radio {
  display: none;
}

.radio-kate:checked + label .block-kate {
  transform: translateY(-20%);
  transition: all 0.2s;
}
.radio-steve:checked + label .block-steve {
  transform: translateY(-20%);
  transition: all 0.2s;
}
.radio-hasan:checked + label .block-hasan {
  transform: translateY(-20%);
  transition: all 0.2s;
}
.radio-liza:checked + label .block-liza {
  transform: translateY(-20%);
  transition: all 0.2s;
}
