.news {
  padding: 80px 0 120px 0;
  &__title {
    margin-bottom: 100px;
  }
}

.news-gallery {
  display: grid;
  grid-template-columns: 1fr;
  margin: 0 auto;
  &__block {
    max-width: 263px;
    min-height: 280px;
    justify-self: center;
    &__img-container {
      width: 263px;
      height: 208px;
      position: relative;
      &__data {
        width: 70px;
        height: 68px;
        background-color: $dark-green;
        color: $white;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-evenly;
        position: absolute;
        top: 0;
        right: 0;
        transition: all 0.2s;
      }
    }
    &__text {
      display: flex;
      flex-direction: column;
      align-items: center;
      color: $text;
      &__title {
        font-size: 1.125em; //18px
        line-height: 2.052631578947368em; //39px
        font-weight: 700;
        transition: all 0.2s;
      }
      &__name-comments {
        &__name {
          font-size: 0.875em; //14px
          padding-right: 10px;
        }
        &__comments {
          border-left: 1px solid $text;
          padding-left: 10px;
        }
      }
    }
  }
  @media screen and (min-width: 768px) {
    grid-template-columns: 1fr 1fr;
  }
  @media screen and (min-width: 1440px) {
    grid-template-columns: 1fr 1fr 1fr 1fr;
    max-width: 1157px;
  }
}

.news-gallery__block:hover
  > .news-gallery__block__img-container
  > .news-gallery__block__img-container__data {
  background-color: $green;
  transition: all 0.2s;
}

.news-gallery__block:hover
  > .news-gallery__block__text
  > .news-gallery__block__text__title {
  color: $green;
  transition: all 0.2s;
}
