@font-face {
  font-family: "icomoon";
  src: url("../fonts/icomoon.ttf?lfopv") format("truetype");
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="icon-"],
[class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: "icomoon" !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-laptop:before {
  content: "\e957";
}
.icon-mobile:before {
  content: "\e958";
}
.icon-tablet:before {
  content: "\e95a";
}
.icon-quotes-left:before {
  content: "\e977";
}
.icon-search:before {
  content: "\e986";
}
.icon-point-up:before {
  content: "\ea03";
}
.icon-plus:before {
  content: "\ea0a";
}

@font-face {
  font-family: "FontAwesome";
  src: url("../fonts/fontawesome-webfont.ttf?v=4.7.0") format("truetype");
  font-weight: normal;
  font-style: normal;
}
.fa {
  display: inline-block;
  font: normal normal normal 14px/1 FontAwesome;
  font-size: inherit;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.fa-diamond:before {
  content: "\f219";
}
.fa-envira:before {
  content: "\f299";
}
.fa-lightbulb-o:before {
  content: "\f0eb";
}
.fa-link:before {
  content: "\f0c1";
}
.fa-stop:before {
  content: "\f04d";
}
.fa-arrows-alt:before {
  content: "\f0b2";
}
.fa-angle-left:before {
  content: "\f104";
}
.fa-angle-right:before {
  content: "\f105";
}
