.work {
  padding: 100px 0;
  background-color: $bg-work;

  &__title {
    margin-bottom: 100px;
  }
}

.work-btns {
  display: flex;
  justify-content: center;
  margin-bottom: 70px;
  &__btn {
    min-height: 60px;
    width: 100%;
    padding: 5px 20px;
    color: $text;
    font-size: 0.875em; //14px
    line-height: 3em; //42px
    border: 1px solid #dadada;
    outline: none;
    background-color: transparent;
    transition: all 0.2s;
    &:not(:last-child) {
      border-right: 1px solid transparent;
    }
    &:hover {
      border: 1px solid $green;
    }
    &:focus {
      color: $green;
    }
  }
  @media screen and (min-width: 768px) {
    display: none;
    &__btn {
      width: unset;
      min-width: 60px;
    }
  }
}

.work-btns-tablet {
  display: none;
  @media screen and (min-width: 768px) {
    display: flex;
  }
}

.work-gallery {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr 1fr 1fr;
  margin: 0 auto 70px auto;
  &__img-container {
    justify-self: center;
    width: 285px;
    height: 211px;
    position: relative;
    overflow: hidden;
    &:not(:first-child, :nth-child(2), :nth-child(3)) {
      display: none;
    }
    &-hover {
      padding-top: 40px;
      width: 100%;
      height: 100%;
      background-color: $bg-work;
      position: absolute;
      display: flex;
      flex-direction: column;
      align-items: center;
      top: -100%;
      transition: all 0.3s;
      &::before {
        content: "";
        display: block;
        width: 100%;
        height: 5px;
        background-color: $green;
        position: absolute;
        top: 0;
      }
      &__btns {
        width: 85px;
        display: flex;
        justify-content: space-between;
        margin-bottom: 24px;
        &__block {
          width: 40px;
          height: 40px;
          border: 1px solid $green;
          border-radius: 50%;
          color: $green;
          display: flex;
          align-items: center;
          justify-content: center;
          &:hover {
            color: $white;
            background-color: $green;
          }
        }
      }
      &__title {
        text-transform: uppercase;
        font-weight: 700;
        color: $green;
        font-size: 0.875em; //14px
        line-height: 1.785714285714286; //25px
      }
      &__subtitle {
        font-weight: 300;
        color: $text;
        font-size: 0.75em; //12px
        line-height: 2.083333333333333em; //25px
      }
    }
  }
  @media screen and (min-width: 768px) {
    grid-template-columns: 1fr 1fr;
    width: 570px;
    &__img-container {
      &:nth-child(4),
      &:nth-child(5),
      &:nth-child(6) {
        display: block;
      }
    }
  }
  @media screen and (min-width: 1024px) {
    grid-template-columns: 1fr 1fr 1fr;
    width: 855px;
    &__img-container {
      &:nth-child(7),
      &:nth-child(8),
      &:nth-child(9) {
        display: block;
      }
    }
  }
  @media screen and (min-width: 1440px) {
    grid-template-columns: 1fr 1fr 1fr 1fr;
    width: 1140px;
    &__img-container {
      &:nth-child(10),
      &:nth-child(11),
      &:nth-child(12) {
        display: block;
      }
    }
  }
}

.work-gallery__img-container:hover > .work-gallery__img-container-hover {
  top: 0;
  transition: all 0.3s;
}
