.options {
  display: grid;
  max-width: 1920px;
  margin: 0 auto;
  &__option {
    width: 100%;
    min-height: 370px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    padding: 100px 0 100px 0;
    transition: all 0.3s;
    text-transform: uppercase;
    font-weight: 700;
    text-align: center;
    &__ico {
      font-size: 4em;
    }
  }
  &__option:nth-child(odd) {
    background-color: $dark-green;
    color: $green;
    &:hover {
      background-color: $white;
      color: $dark-green;
    }
  }
  &__option:nth-child(even) {
    background-color: $green;
    color: $white;
    &:hover {
      background-color: $white;
      color: $dark-green;
    }
  }
  @media screen and (min-width: 768px) {
    grid-template-columns: 1fr 1fr;
    &__option {
      font-size: 1.25em; //20px
    }
    &__option:last-child {
      background-color: $dark-green;
      color: $green;
    }
    &__option:nth-last-child(2) {
      background-color: $green;
      color: $white;
    }
  }
  @media screen and (min-width: 1024px) {
    grid-template-columns: 1fr 1fr 1fr 1fr;
    &__option:nth-child(odd) {
      background-color: $dark-green;
      color: $green;
    }
    &__option:nth-child(even) {
      background-color: $green;
      color: $white;
    }
  }
  &__devices {
    position: relative;
    width: 100px;
    height: 80px;
    &__laptop {
      position: absolute;
      left: 0;
      right: 0;
      font-size: 4.5em;
    }
    &__mobile {
      position: absolute;
      font-size: 2em;
      bottom: -20%;
      right: -20%;
    }
    &__tablet {
      position: absolute;
      bottom: -20%;
      left: -28%;
      font-size: 3em;
    }
  }
}
